@import url(https://fonts.googleapis.com/css?family=Raleway:300);
@font-face {
  font-family:circularBlack ;
  src: url(./font/CircularStd-Black.otf);
}
@font-face {
  font-family: circularLight;
  src: url(./font/CircularStd-Light.otf);
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}


hr{
  border: 1.5px #81818186 solid;
}

/* Header Section */

.header {
    position: fixed;
    height: 90px;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
    transition: .3s ease-in;
    overflow: hidden;
    
}

.header-bg{
  background-color: rgba(0,0,0,.9);
}

.header .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1240px;
    margin: auto;
    height: 100%;
    padding: 0 1rem;
}

.header .nav-menu a {
    color: #ffffff;
    text-shadow: 0px 2px 5px rgba(0, 0, 0, 0.562);
}

.header .navbar img {
    width: 250px;
    height: auto;
}

.header .nav-menu {
    font-family: circularBlack;
    font-size: 1.3em;
    display: flex;
}

.header .nav-item {
    padding: 1.1rem;
    font-weight: 400;
}

.header .nav-item a:hover {
    padding-bottom: 12px;
    border-bottom: 3px solid var(--secondary-color);
}

.hamburger {
    display: none;
}


@media screen and (max-width:940px) {
    .header {
        height: 75px;
        max-width: 100%;
        background-color: rgba(0,0,0,.9);
    }

    .header .navbar {
        max-width: 100%;
    }

    .hamburger {
        display: block;
    }

    .nav-menu {
        position: fixed;
        left: -100%;
        top: 90px;
        flex-direction: column;
        background-color: rgba(0,0,0,.9);
        width: 100%;
        height: 90vh;
        z-index: 999;
        text-align: center;
        transition: .3s;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-item {
        margin: 1.5rem 0;
    }

    .header .navbar img {
        width: 150px;
    }
  }
  
  


  /* button */

  button {
    transform: translateX(0%) translateY(0%);
 }
 
  button {
    color: #292929;
    width: 130px;
    height: 40px;
    border: 0;
    font-size: 18px;
    border: 2px #11111182 solid;
    border-radius: 4px;
    font-family: 'Raleway', sans-serif;
    transition: 0.6s;
    overflow: hidden;
 }
  button:focus {
    outline: 0;
 }
  button:before {
    content: '';
    display: block;
    position: absolute;
    background: rgba(255, 255, 255, 0.5);
    width: 60px;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0.5;
    filter: blur(30px);
    transform: translateX(-100px) skewX(-15deg);
 }
  button:after {
    content: '';
    display: block;
    position: absolute;
    background: rgba(255, 255, 255, 0.2);
    width: 30px;
    height: 100%;
    left: 30px;
    top: 0;
    opacity: 0;
    filter: blur(5px);
    transform: translateX(-100px) skewX(-15deg);
    
 }
  button:hover {
    background: #046F9C;
    cursor: pointer;
    color: #ffffff;
 }
  button:hover:before {
    transform: translateX(150px) skewX(-15deg);
    opacity: 0.6;
    transition: 0.7s;
 }
  button:hover:after {
    transform: translateX(150px) skewX(-15deg);
    opacity: 1;
    transition: 0.7s;
 }


