/* Section Four */

.s4container{
  padding: 15px;
  width: 80% ;
  height: 75%;
  background-color: #c2c2c24d;
  display: flex; 
  justify-content: center;
  flex-wrap: wrap;
  overflow: hidden;
}
.countrycard{
  margin: 0.2em;
  padding: 16px;
  display:flex ;
  align-items: center;
  background-color: #0000006f;
  max-width: 24em;
 
  aspect-ratio: calc(4/1.9);
 
  border-radius: 2%;
}

.countrycard .image{
  margin-right: 1rem;
  border-radius: 2%;
  box-shadow: 0 8px 24px rgb(0 0 0 50%); 
}

.image img{
  object-fit: cover;
  filter: brightness(0.85) grayscale(0.25) invert(0.05) saturate(1.15) ;
}


.countrycard .countrycardcontent{
  height: auto;
  display:flex ;
  flex-direction: column;
  overflow: hidden;
}
.countrycardcontent .title{
  font-family: circularBlack;
  color: rgb(255, 255, 255);
  font-size: 1rem;
  margin-bottom: 6px;
  overflow: hidden;
}
.countrycardcontent .description{
  font-family: circularLight;
  color: #cccccc;
  font-size: 0.875rem;
  overflow: hidden; 
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.head .title{
  font-family: circularBlack ;
